___CSS_LOADER_EXPORT___.push([module.id, `/* D-VRE Extension Styles */

.dvre-widget {
  min-height: 400px;
  overflow: auto;
}

.dvre-widget button:hover {
  opacity: 0.8;
  transform: translateY(-1px);
  transition: all 0.2s ease;
}

.dvre-widget h1 {
  background: linear-gradient(45deg, var(--jp-brand-color1), var(--jp-brand-color2));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Dark mode adjustments */
body[data-jp-theme-light="false"] .dvre-widget {
  background: var(--jp-layout-color0);
}

/* Responsive design */
@media (max-width: 768px) {
  .dvre-widget div[style*="display: flex"] {
    flex-direction: column !important;
    align-items: center;
  }
  
  .dvre-widget button {
    width: 200px;
    margin: 5px 0;
  }
}
