isplay: flex;\n  flex: 1;\n  gap: var(--jp-content-link-margin);\n  min-height: 600px;\n}\n\n.dvre-graph-controls-panel {\n  width: 300px;\n  display: flex;\n  flex-direction: column;\n  gap: var(--jp-content-link-margin);\n  flex-shrink: 0;\n}\n\n.dvre-graph-visualization {\n  flex: 1;\n  position: relative;\n  min-width: 0;\n}\n\n/* Graph Controls */\n.dvre-graph-controls {\n  background: var(--jp-layout-color2);\n  border: var(--jp-border-width) solid var(--jp-border-color1);\n  border-radius: var(--jp-border-radius);\n  padding: var(--jp-cell-padding);\n}\n\n.dvre-graph-stats {\n  display: flex;\n  gap: var(--jp-content-link-margin);\n  margin-bottom: var(--jp-content-link-margin);\n}\n\n.dvre-stat-item {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  flex: 1;\n  padding: 12px;\n  background: var(--jp-layout-color1);\n  border-radius: var(--jp-border-radius);\n  border: var(--jp-border-width) solid var(--jp-border-color1);\n}\n\n.dvre-stat-value {\n  font-size: var(--jp-ui-font-size4);\n  font-weight: var(--jp-ui-font-weight2);\n  color: var(--jp-ui-font-color0);\n  line-height: 1;\n}\n\n.dvre-stat-label {\n  font-size: var(--jp-ui-font-size0);\n  color: var(--jp-ui-font-color2);\n  text-transform: uppercase;\n  font-weight: var(--jp-ui-font-weight1);\n  margin-top: 4px;\n}\n\n.dvre-control-buttons {\n  display: flex;\n  gap: 8px;\n}\n\n.dvre-control-button {\n  flex: 1;\n  padding: 8px 12px;\n  border: var(--jp-border-width) solid var(--jp-border-color1);\n  border-radius: var(--jp-border-radius);\n  font-size: var(--jp-ui-font-size1);\n  font-weight: var(--jp-ui-font-weight1);\n  font-family: var(--jp-ui-font-family);\n  cursor: pointer;\n  transition: all 0.2s ease;\n  background: var(--jp-layout-color1);\n  color: var(--jp-ui-font-color1);\n}\n\n.dvre-control-button:hover {\n  background: var(--jp-layout-color2);\n  border-color: var(--jp-border-color0);\n}\n\n.dvre-control-button.refresh {\n  background: var(--jp-brand-color1);\n  color: white;\n  border-color: var(--jp-brand-color1);\n}\n\n.dvre-control-button.refresh:hover {\n  background: var(--jp-brand-color0);\n  border-color: var(--jp-brand-color0);\n}\n\n.dvre-control-button.export {\n  background: var(--jp-success-color1);\n  color: white;\n  border-color: var(--jp-success-color1);\n}\n\n.dvre-control-button.export:hover {\n  background: var(--jp-success-color0);\n  border-color: var(--jp-success-color0);\n}\n\n/* Graph Filters */\n.dvre-graph-filters {\n  background: var(--jp-layout-color2);\n  border: var(--jp-border-width) solid var(--jp-border-color1);\n  border-radius: var(--jp-border-radius);\n  padding: var(--jp-cell-padding);\n}\n\n.dvre-graph-filters h3 {\n  margin: 0 0 var(--jp-content-link-margin) 0;\n  color: var(--jp-ui-font-color0);\n  font-size: var(--jp-ui-font-size2);\n  font-weight: var(--jp-ui-font-weight1);\n  font-family: var(--jp-ui-font-family);\n}\n\n.dvre-filter-section {\n  margin-bottom: var(--jp-content-link-margin);\n}\n\n.dvre-filter-section:last-child {\n  margin-bottom: 0;\n}\n\n.dvre-filter-section h4 {\n  margin: 0 0 12px 0;\n  color: var(--jp-ui-font-color1);\n  font-size: var(--jp-ui-font-size1);\n  font-weight: var(--jp-ui-font-weight1);\n  font-family: var(--jp-ui-font-family);\n}\n\n.dvre-filter-checkboxes {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.dvre-checkbox-label {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  cursor: pointer;\n  font-size: var(--jp-ui-font-size1);\n  color: var(--jp-ui-font-color1);\n  font-family: var(--jp-ui-font-family);\n}\n\n.dvre-checkbox-label input[type=\"checkbox\"] {\n  margin: 0;\n  accent-color: var(--jp-brand-color1);\n}\n\n.dvre-connection-type {\n  display: inline-block;\n  padding: 2px 8px;\n  border-radius: var(--jp-border-radius);\n  font-size: var(--jp-ui-font-size0);\n  font-weight: var(--jp-ui-font-weight1);\n  text-transform: capitalize;\n}\n\n.dvre-connection-type.members {\n  background: #f39c12;\n  color: white;\n}\n\n.dvre-connection-type.data {\n  background: #27ae60;\n  color: white;\n}\n\n.dvre-connection-type.models {\n  background: #9b59b6;\n  color: white;\n}\n\n.dvre-connection-type.infrastructure {\n  background: #e74c3c;\n  color: white;\n}\n\n.dvre-connection-slider {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n}\n\n.dvre-slider {\n  flex: 1;\n  accent-color: var(--jp-brand-color1);\n}\n\n.dvre-slider-value {\n  font-weight: var(--jp-ui-font-weight1);\n  color: var(--jp-ui-font-color0);\n  min-width: 24px;\n  text-align: center;\n  font-family: var(--jp-ui-font-family);\n}\n\n.dvre-filter-description {\n  margin: 8px 0 0 0;\n  font-size: var(--jp-ui-font-size0);\n  color: var(--jp-ui-font-color2);\n  line-height: var(--jp-content-line-height);\n  font-family: var(--jp-ui-font-family);\n}\n\n.dvre-project-controls {\n  display: flex;\n  gap: 8px;\n  margin-bottom: 12px;\n}\n\n.dvre-filter-button {\n  padding: 6px 12px;\n  border: var(--jp-border-width) solid var(--jp-border-color1);\n  border-radius: var(--jp-border-radius);\n  background: var(--jp-layout-color1);\n  color: var(--jp-ui-font-color1);\n  font-size: var(--jp-ui-font-size0);\n  font-family: var(--jp-ui-font-family);\n  cursor: pointer;\n  transition: all 0.2s ease;\n}\n\n.dvre-filter-button:hover {\n  background: var(--jp-layout-color2);\n  border-color: var(--jp-border-color0);\n}\n\n.dvre-filter-button.secondary {\n  background: var(--jp-layout-color2);\n}\n\n.dvre-filter-button.small {\n  padding: 4px 8px;\n  font-size: var(--jp-ui-font-size0);\n}\n\n.dvre-project-list {\n  max-height: 200px;\n  overflow-y: auto;\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\n.dvre-project-name {\n  font-size: var(--jp-ui-font-size1);\n  color: var(--jp-ui-font-color1);\n  font-family: var(--jp-ui-font-family);\n}\n\n.dvre-quick-filters {\n  display: flex;\n  flex-direction: column;\n  gap: 6px;\n}\n\n/* Graph SVG */\n.dvre-graph-svg {\n  background: var(--jp-layout-color0);\n  border: v